const Regions = () => {
	const [selectedTab, setSelectedTab] = React.useState("Київ");

	return (
		<div className="regions">
			<div className="regions__header">
				<h2 className="regions__title title">Регіони</h2>
				<a href="#" className="regions__link" aria-label="перейти на сторінку всіх новин розділу">
					Всі новини розділу
				</a>
			</div>
			<div className="regions__body">
				<Swiper
					className="regions__slider regions-slider"
					wrapperTag="ul"
					spaceBetween={16}
					speed={1000}
					watchSlidesProgress
					slidesPerView={"auto"}
					slidesOffsetBefore={24}
					slidesOffsetAfter={24}
					allowTouchMove={false}
					breakpoints={{
						992: {
							slidesOffsetBefore: 0,
							slidesOffsetAfter: 0,
						},
						1400: {
							allowTouchMove: true,
							slidesOffsetBefore: 0,
							slidesOffsetAfter: 0,
						},
					}}
				>
					<div slot="container-start">
						<RegionsSliderTablist
							className="regions__tablist"
							tablist={["Київ", "Одеса", "Харків"]}
							selectedTab={selectedTab}
							changeSelectedTab={setSelectedTab}
						/>
					</div>
					<SwiperSlide className="regions-slider__slide" tag="li">
						<CardRegionSlide city="Київ" />
					</SwiperSlide>
					<SwiperSlide className="regions-slider__slide" tag="li">
						<CardRegionSlide city="Одеса" />
					</SwiperSlide>
					<SwiperSlide className="regions-slider__slide" tag="li">
						<CardRegionSlide city="Харків" />
					</SwiperSlide>
				</Swiper>
			</div>
		</div>
	);
};

export default Regions;
