import React from "react";

import ItemVideo from "../itemVideo/ItemVideo";
import MoreButton from "../moreButton/MoreButton";
import LoadMoreButton from "../loadMoreButton/LoadMoreButton";

import "./sectionVideos.scss";
import img01 from "../../assets/images/section-three/01.jpg";
import img02 from "../../assets/images/section-three/02.jpg";
import img03 from "../../assets/images/section-three/03.jpg";
import img04 from "../../assets/images/section-three/04.jpg";
import img05 from "../../assets/images/section-three/05.jpg";

const videos = [
	{
		title: '"Це не Джошуа": Усик розповів, кого вважає своїм головним суперником',
		dateTime: "2022-08-05T11:00",
		url: "#",
		imageSrc: img01,
		alt: "Усик з Джошуа тет-а-тет",
	},
	{
		title: 'Ватажок "ДНР" підтвердив підготовку самосуду над полоненими українськими військовими. Відео',
		dateTime: "2022-08-05T13:02",
		url: "#",
		imageSrc: img02,
		alt: "Пушистін",
	},
	{
		title: '"Спекотні літні канікули": Міноборони України попередило росіян про наслідки відвідування Криму (відео)',
		dateTime: "2022-08-06T10:20",
		url: "#",
		imageSrc: img03,
		alt: "бавовна в Криму",
	},
	{
		title: "Під москвою палала військова частина, де проходять службу строковики (відео)",
		dateTime: "2022-08-05T21:00",
		url: "#",
		imageSrc: img04,
		alt: "мрія українця - москва горить",
	},
	{
		title: 'СБУ затримала в Києві "військового експерта", який працював на росію (фото)',
		dateTime: "2022-08-01T01:30",
		url: "#",
		imageSrc: img05,
		alt: "Допит СБУ",
	},
];

const SectionVideos = () => {
	return (
		<section className="section-three">
			<div className="container">
				<div className="section-three__inner">
					<div className="section-three__header">
						<h2 className="section-three__title title">Відео</h2>
						<MoreButton className="section-three__more" label="Більше відео" url="#" />
						<a href="#" className="section-three__more-videos">
							Більше відео
						</a>
					</div>
					<div className="section-three__content">
						{videos && videos.length && (
							<ul className="section-three__list">
								{videos.map((video, index) => (
									<ItemVideo
										key={video.title}
										{...video}
										className="section-three__item"
										isLarge={index === 0}
									/>
								))}
							</ul>
						)}
						<LoadMoreButton className={"section-three__button"} title={"Більше"} withoutIcon />
					</div>
				</div>
			</div>
		</section>
	);
};

export default SectionVideos;
